<template>
  <div class="product-detail">
    <div v-if="product" class="product-info">
      <div class="info-img">
        <img :src="product.image" class="product-img" />
      </div>
      <div class="info-des">
        <h1>{{ product.name }}</h1>
        <p>{{ product.description }}</p>
        <p><span>${{ product.price }}</span> NTD / 個</p>
        <button class="btn-primary" @click="addToCart(product)">加入購物車</button>
        <button @click="$router.push('/menu')" class="btn-back">返回菜單</button>
      </div>
    </div>

    <!-- 其他產品區塊 -->
    <div v-if="relatedProducts.length > 0" class="related-products">
      <h2>其他產品</h2>
      <div class="info-area">
      <button class="btn-trun" @click="prevPage" :disabled="currentPage === 0">◀</button>
      <div class="product-gallery">
        <div 
          v-for="(relatedProduct, index) in relatedProducts" 
          :key="index" 
          class="product-thumbnail"
        >
          <!-- 使用 router-link 導向到相對應的商品頁面 -->
          <router-link :to="'/menu/' + relatedProduct.id" class="related-link">
            <img :src="relatedProduct.image" :alt="relatedProduct.name" />
            <p>{{ relatedProduct.name }}</p>
          </router-link>
        </div>
      </div>
      <button class="btn-trun" @click="nextPage" :disabled="(currentPage + 1) * 5 >= menuData.length">▶</button>
      </div>
    </div>

    <div v-else>
      <p>找不到該商品</p>
    </div>
  </div>
</template>

<script>
import { ref, inject, computed } from 'vue';
import { useRoute } from 'vue-router';
import { useStore } from 'vuex'; // 引入 Vuex

export default {
  setup() {
    const route = useRoute();
    const store = useStore(); // 使用 Vuex store
    const menuData = inject('menuData') || ref([]);
    const product = computed(() => menuData.value.find(item => item.id == route.params.id));

    // 控制顯示頁面
    const currentPage = ref(0); // 頁數，初始為 0
    const pageSize = 3; // 每次顯示的產品數量

    // 相關產品
    const relatedProducts = computed(() => {
      if (product.value) {
        const filteredProducts = menuData.value.filter(item => item.id !== product.value.id); // 篩選出不同於當前商品的產品
        return filteredProducts.slice(currentPage.value * pageSize, (currentPage.value + 1) * pageSize);
      }
      return [];
    });

    // 顯示下一頁的功能
    const nextPage = () => {
      if ((currentPage.value + 1) * pageSize < menuData.value.length) {
        currentPage.value++;
      }
    };

    // 顯示上一頁的功能
    const prevPage = () => {
      if (currentPage.value > 0) {
        currentPage.value--;
      }
    };

    // 加入購物車
    const addToCart = () => {
      if (product.value) {
        store.commit('addToCart', product.value); // 直接呼叫 Vuex mutation
        localStorage.setItem('cart', JSON.stringify(store.state.cart)); // 更新 localStorage
        console.log('加入購物車:', product.value);
      }
    };

    return { product, addToCart, relatedProducts, menuData, currentPage, nextPage, prevPage };
  }
};
</script>
